import React, {useState, useEffect} from 'react';
import Preloader from "../../../common/preloader/preloader";


const ProfileStatusWithHooks = (props) => {
    let [editMode, setEditMode] = useState(false);
    let [stateStatus, editStateStatus] = useState(props.status)
    let deActivateEditMode = () => {
        props.updateStatusProfile(stateStatus)
        setEditMode(false)
    }

    useEffect(() => {
        editStateStatus(props.status)
    },[props.status])

    if (props.isFetching === true) {
        return <Preloader/>
    } else {
        return (
            <div>
                {!editMode &&
                <div>
                    <span  onDoubleClick={() => setEditMode(true)}>{props.status}</span>
                </div>
                }
                {editMode &&
                <div>
                    <input autoFocus={true} onBlur={deActivateEditMode} value={stateStatus}
                           onChange={(e) => {
                               editStateStatus(e.target.value)
                           }}/>
                </div>
                }
            </div>
        )
    }
}

export default ProfileStatusWithHooks;